import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Form from 'react-bootstrap/Form';
import './css/Modal2.css';
import axios from 'axios';
import { useNavigate } from "react-router-dom";

function Modal2() {
  const navigate = useNavigate();
  const [card_own_yn, setCard_own_yn] = useState('');
  const [card_purpose, setCard_purpose] = useState('');
  const [prefer_benefit, setPrefer_benefit] = useState('');
  const [most_benefit, setMost_benefit] = useState('');
  const [airport_mileage, setAirport_mileage] = useState('');
  const [prefer_design, setPrefer_design] = useState('');
  const [card_year_fee, setCard_year_fee] = useState('');
  const [last_monthly_expense, setLast_monthly_expense] = useState('');
  const [this_monthly_expense, setThis_monthly_expense] = useState('');
  const [step, setStep] = useState(1);

  //   
  const handlePrevClick = () => {
    if (step === 6) {
      setStep(1);
    } else {
      setStep(prevStep => prevStep - 1);
    }
  };  

  

  //   2  
  const handleNextClick1 = () => {
    if (!card_purpose) { 
      window.alert("    .");
  } else {
      setStep(step + 1);
  }
};


  //   3  
  const handleNextClick2 = () => {
    if (!prefer_benefit) { 
      window.alert("    .");
  } else {
      setStep(step + 1);
  }
};

//   4 
const handleNextClick = () => {
  if (step === 4) {
    //   
    let selectedValue;
    switch (prefer_benefit) {
      case '1':
        selectedValue = most_benefit;
        break;
      case '2':
        selectedValue = airport_mileage;
        break;
      case '3':
        selectedValue = prefer_design;
        break;
      case '4':
        selectedValue = card_year_fee;
        break;
      case '5':
        selectedValue = last_monthly_expense;
        break;
      default:
        selectedValue = '';
        break;
    }

    //      
    if (!selectedValue) {
      alert("    .");
      return;
    }
  }

  //   
  setStep(step + 1);
};


  const handleFirstClick = () => {
    if (card_own_yn === '1') {
      setStep(6);
    } else if (card_own_yn === '2') {
      setStep(2);
    } else if (!card_own_yn) {
      alert("    .");
    } 
  };
  
  const dataToSend = {
    cardOwnYn: parseInt(card_own_yn),
    cardPurpose: parseInt(card_purpose),
    preferBenefit: parseInt(prefer_benefit),
    mostBenefit: parseInt(most_benefit),
    airportMileage: parseInt(airport_mileage),
    preferDesign: parseInt(prefer_design),
    cardYearFee: parseInt(card_year_fee),
    lastMonthExpense: parseInt(last_monthly_expense),
    thisMonthExpense: parseInt(this_monthly_expense)
  };
  try {
    const response = axios.post("http://localhost:8080/survey/regist", dataToSend);
    if (response.data.success) {
      console.log("Data sent successfully", response.data);
    } else {
      console.error("Error sending data", response.data.message);
    }
  } catch (error) {
    console.error("There was an error sending the data", error);
  }


  const [show, setShow] = useState(false);
  const [loading, setLoading] = useState(false);
  const [darkenBackground, setDarkenBackground] = useState(false); 

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!this_monthly_expense || this_monthly_expense === '') {
      alert('    .');
      return;
    }

    console.log("Form Submitted");
    setLoading(true);
    setDarkenBackground(true);

    await new Promise(res => setTimeout(res, 2000));

    setLoading(false);
    navigate("/credit/recommend");
    setShow(false);
    setDarkenBackground(false);
  };

  return (
    <>
      <Button variant="primary" onClick={() => setShow(true)}>
          
      </Button>
      <div className={`modal-overlay ${darkenBackground ? 'darken' : ''}`}>
        <div className='totalmodal'>
      <Modal show={show} onHide={() => setShow(false)} className="modal2_1" aria-labelledby="example-custom-modal-styling-title" centered={true}>
      <Modal.Header closeButton>
        <br/>
        <div className="modalfont1">
            
          </div>
      </Modal.Header>
        <br/>
        <Modal.Body style={{ overflowY: 'auto', maxHeight: '700px', maxWidth:'1200px'}}>
          <p className='modalfont2'>
                   .
          </p>
          <hr></hr>
          <Form onSubmit={handleSubmit}>
          
          {step === 1 && (
            <Form.Group className='modalfont3'>
              <Form.Label>1.    ?</Form.Label>
              <Form.Check
                type="radio"
                label=""
                name="card_own_yn"
                value="1"
                checked={card_own_yn === '1'}
                onChange={(e) => {setCard_own_yn(e.target.value);}}
              />
              <Form.Check
                type="radio"
                label=""
                name="card_own_yn"
                value="2"
                checked={card_own_yn === '2'}
                onChange={(e) => {setCard_own_yn(e.target.value);}}
              />
              <div className="modalButtonContainer">
                <Button className="modalButton" type="button" onClick={handleFirstClick}></Button>
              </div>
            </Form.Group>
          )}

          
          { card_own_yn === "2" && step === 2 && ( 
          <Form.Group className='modalfont3'>
            <Form.Label>2.    ?</Form.Label>
            <Form.Check 
              type="radio" 
              label="  " 
              name="card_purpose" 
              value="1"
              checked={card_purpose === '1'}
              onChange={(e) => setCard_purpose(e.target.value)}
            />
            <Form.Check 
              type="radio" 
              label=" " 
              name="card_purpose" 
              checked={card_purpose === '2'}
              value="2"
              onChange={(e) => setCard_purpose(e.target.value)}
            />
            <Form.Check 
              type="radio" 
              label=" " 
              name="card_purpose" 
              checked={card_purpose === '3'}
              value="3"
              onChange={(e) => setCard_purpose(e.target.value)}
            />
            <Form.Check 
              type="radio" 
              label="" 
              name="card_purpose"
              checked={card_purpose === '4'} 
              value="4"
              onChange={(e) => setCard_purpose(e.target.value)}
            />
            <div className="modalButtonContainer">
                <Button className="modalButton" type="button" onClick={handlePrevClick}></Button>
                <Button className="modalButton" type="button" onClick={handleNextClick1}></Button>
              </div>
          </Form.Group>
          )}
          
            {step === 3 && (
          <Form.Group className='modalfont3'>
            <Form.Label>3.   ,    ?</Form.Label>
            <Form.Check 
              type="radio" 
              label="(/)" 
              name="prefer_benefit" 
              checked={prefer_benefit === '1'}
              value="1"
              onChange={(e) => {setPrefer_benefit(e.target.value); setStep(3);}}
            />
            <Form.Check 
              type="radio" 
              label="" 
              name="prefer_benefit" 
              checked={prefer_benefit === '2'}
              value="2"
              onChange={(e) => {setPrefer_benefit(e.target.value); setStep(3);}}
            />
            <Form.Check 
              type="radio" 
              label="" 
              name="prefer_benefit" 
              checked={prefer_benefit === '3'}
              value="3"
              onChange={(e) => {setPrefer_benefit(e.target.value); setStep(3);}}
            />
            <Form.Check 
              type="radio" 
              label="" 
              name="prefer_benefit" 
              checked={prefer_benefit === '4'}
              value="4"
              onChange={(e) => {setPrefer_benefit(e.target.value); setStep(3);}}
            />
            <Form.Check 
              type="radio" 
              label=" " 
              name="prefer_benefit" 
              checked={prefer_benefit === '5'}
              value="5"
              onChange={(e) => {setPrefer_benefit(e.target.value); setStep(3);}}
            />
              <div className="modalButtonContainer">
                <Button className="modalButton" type="button" onClick={handlePrevClick}></Button>
                <Button className="modalButton" type="button" onClick={handleNextClick2}></Button>
              </div>
          </Form.Group>
          )} 
          

          { prefer_benefit === '1' && step === 4 &&(
              <Form.Group className='modalfont3'>
                <Form.Label>4.        ?</Form.Label>
                <Form.Check 
                    type="radio" 
                    label="" 
                    name="most_benefit" 
                    checked={most_benefit === '1'}
                    value="1"
                    onChange={(e) => setMost_benefit(e.target.value)}
                />
                <Form.Check 
                    type="radio" 
                    label="" 
                    name="most_benefit" 
                    checked={most_benefit === '2'}
                    value="2"
                    onChange={(e) => setMost_benefit(e.target.value)}
                />
                <Form.Check 
                    type="radio" 
                    label="" 
                    name="most_benefit" 
                    checked={most_benefit === '3'}
                    value="3"
                    onChange={(e) => setMost_benefit(e.target.value)}
                />
                <Form.Check 
                    type="radio" 
                    label="" 
                    name="most_benefit"
                    checked={most_benefit === '4'} 
                    value="4"
                    onChange={(e) => setMost_benefit(e.target.value)}
                />
                <Form.Check 
                    type="radio" 
                    label="" 
                    name="most_benefit"
                    checked={most_benefit === '5'}
                    value="5"
                    onChange={(e) => setMost_benefit(e.target.value)}
                />
                <Form.Check 
                    type="radio" 
                    label="" 
                    name="most_benefit" 
                    checked={most_benefit === '6'}
                    value="6"
                    onChange={(e) => setMost_benefit(e.target.value)}
                />
                <Form.Check 
                    type="radio" 
                    label="" 
                    name="most_benefit" 
                    checked={most_benefit === '7'}
                    value="7"
                    onChange={(e) => setMost_benefit(e.target.value)}
                />
                <Form.Check 
                    type="radio" 
                    label=" " 
                    name="most_benefit"
                    checked={most_benefit === '8'} 
                    value="8"
                    onChange={(e) => setMost_benefit(e.target.value)}
                />
                <Form.Check 
                    type="radio" 
                    label=" " 
                    name="most_benefit" 
                    checked={most_benefit === '9'}
                    value="9"
                    onChange={(e) => setMost_benefit(e.target.value)}
                />
              <div className="modalButtonContainer">
                <Button className="modalButton" type="button" onClick={handlePrevClick}></Button>
                <Button className="modalButton" type="button" onClick={handleNextClick}></Button>
              </div>
            </Form.Group>
            )}

          { prefer_benefit === '2' && step === 4 &&( 
            <Form.Group className='modalfont3'>
              <Form.Label>4.   ,     ?</Form.Label>
              <Form.Check 
                  type="radio" 
                  label="" 
                  name="airport_mileage" 
                  checked={airport_mileage === '1'}
                  value="1"
                  onChange={(e) => setAirport_mileage(e.target.value)}
              />
              <Form.Check 
                  type="radio" 
                  label="" 
                  name="airport_mileage" 
                  checked={airport_mileage === '2'}
                  value="2"
                  onChange={(e) => setAirport_mileage(e.target.value)}
              />
              <Form.Check 
                  type="radio" 
                  label=" " 
                  name="airport_mileage" 
                  checked={airport_mileage === '3'}
                  value="3"
                  onChange={(e) => setAirport_mileage(e.target.value)}
              />
              <Form.Check 
                  type="radio" 
                  label=" " 
                  name="airport_mileage" 
                  checked={airport_mileage === '4'}
                  value="4"
                  onChange={(e) => setAirport_mileage(e.target.value)}
              />
              <div className="modalButtonContainer">
                <Button className="modalButton" type="button" onClick={handlePrevClick}></Button>
                <Button className="modalButton" type="button" onClick={handleNextClick}></Button>
              </div>
            </Form.Group>
            )}

          { prefer_benefit === '3' && step === 4 &&(
            <Form.Group className='modalfont3'>
              <Form.Label>4.   ,    ?</Form.Label>
                <Form.Check 
                  type="radio" 
                  label="" 
                  name="prefer_design" 
                  checked={prefer_design === '1'}
                  value="1"
                  onChange={(e) => setPrefer_design(e.target.value)}
              />
                <Form.Check 
                  type="radio" 
                  label="" 
                  name="prefer_design" 
                  checked={prefer_design === '2'}
                  value="2"
                  onChange={(e) => setPrefer_design(e.target.value)}
              />
                <Form.Check 
                  type="radio" 
                  label="  " 
                  name="prefer_design" 
                  checked={prefer_design === '3'}
                  value="3"
                  onChange={(e) => setPrefer_design(e.target.value)}
              />
                <Form.Check 
                  type="radio" 
                  label="" 
                  name="prefer_design" 
                  checked={prefer_design === '4'}
                  value="4"
                  onChange={(e) => setPrefer_design(e.target.value)}
              />
                <Form.Check 
                  type="radio" 
                  label="" 
                  name="prefer_design" 
                  checked={prefer_design === '5'}
                  value="5"
                  onChange={(e) => setPrefer_design(e.target.value)}
              />
              <div className="modalButtonContainer">
                <Button className="modalButton" type="button" onClick={handlePrevClick}></Button>
                <Button className="modalButton" type="button" onClick={handleNextClick}></Button>
              </div>
            </Form.Group>
            )}

          { prefer_benefit === '4' && step === 4 &&(
            <Form.Group className='modalfont3'>
              <Form.Label>4.   ,    ?</Form.Label>
              <Form.Check 
                type="radio" 
                label="  " 
                name="card_year_fee" 
                checked={card_year_fee === '1'}
                value="1"
                onChange={(e) => setCard_year_fee(e.target.value)}
            />
              <Form.Check 
                type="radio" 
                label="30,000 " 
                name="card_year_fee" 
                checked={card_year_fee === '2'}
                value="2"
                onChange={(e) =>  setCard_year_fee(e.target.value)}
            />
              <Form.Check 
                type="radio" 
                label="50,000 " 
                name="card_year_fee" 
                checked={card_year_fee === '3'}
                value="3"
                onChange={(e) =>  setCard_year_fee(e.target.value)}
            />
              <Form.Check 
                type="radio" 
                label=" " 
                name="card_year_fee" 
                checked={card_year_fee === '4'}
                value="4"
                onChange={(e) =>  setCard_year_fee(e.target.value)}
            />
              <div className="modalButtonContainer">
                <Button className="modalButton" type="button" onClick={handlePrevClick}></Button>
                <Button className="modalButton" type="button" onClick={handleNextClick}></Button>
              </div>
            </Form.Group>
            )}

          { prefer_benefit === '5' && step === 4 &&(
            <Form.Group className='modalfont3'>
              <Form.Label>4.   ,     ?</Form.Label>
              <Form.Check 
                  type="radio" 
                  label=" " 
                  name="last_monthly_expense" 
                  checked={last_monthly_expense === '1'}
                  value="1"
                  onChange={(e) => setLast_monthly_expense(e.target.value)}
              />
              <Form.Check 
                  type="radio" 
                  label="30 " 
                  name="last_monthly_expense" 
                  checked={last_monthly_expense === '2'}
                  value="2"
                  onChange={(e) => setLast_monthly_expense(e.target.value)}
              />
              <Form.Check 
                  type="radio" 
                  label="40 " 
                  name="last_monthly_expense" 
                  checked={last_monthly_expense === '3'}
                  value="3"
                  onChange={(e) => setLast_monthly_expense(e.target.value)}
              />
              <Form.Check 
                  type="radio" 
                  label="50 " 
                  name="last_monthly_expense"
                  checked={last_monthly_expense === '4'} 
                  value="4"
                  onChange={(e) => setLast_monthly_expense(e.target.value)}
              />
              <Form.Check 
                  type="radio" 
                  label=" " 
                  name="last_monthly_expense"
                  checked={last_monthly_expense === '5'} 
                  value="5"
                  onChange={(e) => setLast_monthly_expense(e.target.value)}
              />
              <div className="modalButtonContainer">
                <Button className="modalButton" type="button" onClick={handlePrevClick}></Button>
                <Button className="modalButton" type="button" onClick={handleNextClick}></Button>
              </div>
            </Form.Group>
          )}
             { step === 5 &&(
            <Form.Group className='modalfont3'>
              <Form.Label>5.      ,    .</Form.Label>
              <Form.Check 
                type="radio" 
                label="30 " 
                name="this_monthly_expense" 
                checked={this_monthly_expense === '1'}
                value="1"
                onChange={(e) => setThis_monthly_expense(e.target.value)}
              />
              <Form.Check 
                type="radio" 
                label="50 " 
                name="this_monthly_expense"
                checked={this_monthly_expense === '2'} 
                value="2"
                onChange={(e) => setThis_monthly_expense(e.target.value)}
              />
              <Form.Check 
                type="radio" 
                label="70 " 
                name="this_monthly_expense" 
                checked={this_monthly_expense === '3'}
                value="3"
                onChange={(e) => setThis_monthly_expense(e.target.value)}
              />
              <Form.Check 
                type="radio" 
                label="100 " 
                name="this_monthly_expense" 
                checked={this_monthly_expense === '4'}
                value="4"
                onChange={(e) => setThis_monthly_expense(e.target.value)}
              />
              <Form.Check 
                type="radio" 
                label="100 " 
                name="this_monthly_expense" 
                checked={this_monthly_expense === '5'}
                value="5"
                onChange={(e) => setThis_monthly_expense(e.target.value)}
              />
            <div className="modalButtonContainer">
              <Button className="modalButton" type="button" onClick={handlePrevClick}></Button>
              <Button className="modalButton" type="submit"></Button>
            </div>
            </Form.Group>
            )}

            { card_own_yn === "1" && step === 6 && (
            <>
              <Form.Group className='modalfont3'>
                <Form.Label>2.     ?</Form.Label>
                <Form.Check 
                  type="radio" 
                  label="   " 
                  name="card_purpose" 
                  value="5"
                  checked={card_purpose === '5'}
                  onChange={(e) => setCard_purpose(e.target.value)}
                />
                <Form.Check 
                  type="radio" 
                  label="   " 
                  name="card_purpose" 
                  checked={card_purpose === '6'}
                  value="6"
                  onChange={(e) => setCard_purpose(e.target.value)}
                />
                <Form.Check 
                  type="radio" 
                  label="   " 
                  name="card_purpose" 
                  checked={card_purpose === '7'}
                  value="7"
                  onChange={(e) => setCard_purpose(e.target.value)}
                />
                <Form.Check 
                  type="radio" 
                  label=" " 
                  name="card_purpose"
                  checked={card_purpose === '8'} 
                  value="8"
                  onChange={(e) => setCard_purpose(e.target.value)}
                />
                <Form.Check 
                  type="radio" 
                  label="" 
                  name="card_purpose"
                  checked={card_purpose === '9'} 
                  value="9"
                  onChange={(e) => setCard_purpose(e.target.value)}
                />
              <div className="modalButtonContainer">
                  <Button className="modalButton" type="button" onClick={handlePrevClick}></Button>
                  <Button className="modalButton" type="button" onClick={handleNextClick1}></Button>
                </div>
            </Form.Group>
            </>
            )}
            
            { card_own_yn === "1" && step === 7 && (
            <>
            <Form.Group className='modalfont3'>
              <Form.Label>3.        .</Form.Label>
              <Form.Check 
                type="radio" 
                label="30 " 
                name="this_monthly_expense" 
                checked={this_monthly_expense === '6'}
                value="6"
                onChange={(e) => setThis_monthly_expense(e.target.value)}
              />
              <Form.Check 
                type="radio" 
                label="50 " 
                name="this_monthly_expense" 
                checked={this_monthly_expense === '7'}
                value="7"
                onChange={(e) => setThis_monthly_expense(e.target.value)}
              />
              <Form.Check 
                type="radio" 
                label="70 " 
                name="this_monthly_expense" 
                checked={this_monthly_expense === '8'}
                value="8"
                onChange={(e) => setThis_monthly_expense(e.target.value)}
              />
              <Form.Check 
                type="radio" 
                label="100 " 
                name="this_monthly_expense" 
                checked={this_monthly_expense === '9'}
                value="9"
                onChange={(e) => setThis_monthly_expense(e.target.value)}
              />
              <Form.Check 
                type="radio" 
                label="100 " 
                name="this_monthly_expense" 
                checked={this_monthly_expense === '10'}
                value="10"
                onChange={(e) => setThis_monthly_expense(e.target.value)}
              />
              <div className="modalButtonContainer">
                <Button className="modalButton" type="button" onClick={handlePrevClick}></Button>
              <Button className="modalButton" type="submit"></Button>
            </div>
            </Form.Group>
            </>
            )}
          </Form>
        </Modal.Body>
      </Modal>
      </div>
      </div>
      
      <Modal show={loading} centered={true} className="loadingModal">
        <Modal.Body className="Modal__Body">
            <div className="title">WON</div>
            <div className="info">    .<br/>   .</div>
            <div className="note">     N    .</div>
        </Modal.Body>
        </Modal>
    </>
  );
}

export default Modal2;