import React, { useEffect, useState } from 'react';
import { ToggleButtonGroup, ToggleButton } from 'react-bootstrap';
import axios from 'axios';
import './css/Credit.css';
import CardList from './CardList';

function Credit() {
  useEffect(() => {
    axios.get('http://localhost:8080/cardInfo')
      .then(response => {
        setCardInfo(response.data);
      })
      .catch(error => {
        console.error('API    : ', error);
      });
  }, []);

  const [cardInfo, setCardInfo] = useState([]);
  const [filteredCards, setFilteredCards] = useState([]);
  const [selectedCardCompanys, setselectedCardCompanys] = useState(['']);
  const [selectedBenefits, setselectedBenefits] = useState(['']);

  const handleCardCompanyToggle = (selected1) => {
    console.log("handleCardCompanyToggle selected:", selected1);

    // If "" is selected and there are other selected items, only keep "".
    if (selected1.includes('') && selected1.length > 1) {
        setselectedCardCompanys(['']);
    } else if (!selected1.includes('') && selectedCardCompanys.includes('')) {
        // If any other item is selected and "" was previously selected, remove "".
        setselectedCardCompanys(selected1);
    } else {
        setselectedCardCompanys(selected1);
    }
};

const handleBenefitToggle = (selected2) => {
    console.log("handleBenefitToggle selected:", selected2);

    // If "" is selected and there are other selected items, only keep "".
    if (selected2.includes('') && selected2.length > 1) {
        setselectedBenefits(['']);
    } else if (!selected2.includes('') && selectedBenefits.includes('')) {
        // If any other item is selected and "" was previously selected, remove "".
        setselectedBenefits(selected2);
    } else {
        setselectedBenefits(selected2);
    }
};

  

  useEffect(() => {
    const newFilteredCards = cardInfo.filter((card) => {
      return (
        (selectedCardCompanys.includes('') || selectedCardCompanys.includes(card.cardCorp)) &&
        (selectedBenefits.includes('') || selectedBenefits.some(benefit => card.benefitMate.includes(benefit)))
      );
    });
  
    setFilteredCards(newFilteredCards);
  }, [selectedCardCompanys, selectedBenefits, cardInfo]);

  const cardCompanyItems = ['', '', '', 'KB', '', '', ''];
  const benefitItems = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''];

  return (
    <div className="container">
      <h2 className="credit1"></h2>
      <p className='credit2'></p>
      <ToggleButtons items={cardCompanyItems} selectedItems={selectedCardCompanys} handleToggle={handleCardCompanyToggle} startIndex={0} />
      <br />
      <p className='credit2'></p>
      <ToggleButtons items={benefitItems} selectedItems={selectedBenefits} handleToggle={handleBenefitToggle} startIndex={10} />
      <br/><br/>
      <p className='credit2'> : {filteredCards.length}</p>
      <div className="row">
        {filteredCards.map((card, i) => (
          <CardList card={card} key={i} />
        ))}
      </div>
      <br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
    </div>
  );
}

function ToggleButtons({ items, selectedItems, handleToggle, startIndex = 0 }) {
  return (
    <ToggleButtonGroup type="checkbox" value={selectedItems} className="mb-2" onChange={handleToggle}>
      {items.map((item, index) => (
        <ToggleButton
          className={`toggle-btn${index === 0 ? '' : '1'}`}
          id={`tbg-${index === 0 ? 'check' : 'radio'}-${startIndex + index}`}
          value={item}
          active={selectedItems.includes(item)}
          key={index}
        >
          {item}
        </ToggleButton>
      ))}
    </ToggleButtonGroup>
  );
}

export default Credit;
